import configdata from '../utils/config'

/**/
function initShare(share) {
	let ushare = uni.getStorageSync("ushare");
	let url = location.href.split('#')[0];
	let token = uni.getStorageSync("token");
	if(token){
		if(ushare){
			wxshare(ushare,share);
		}else{
			uni.request({
				url: configdata.APIHOST+"/user/share",
				method: 'post',
				header:{
					"content-type": "application/json; charset=utf-8",
					"token": token
			    },
				data:{
					url:url
				},
				success: res => {
					if (res.data.code == 200) {
						uni.setStorage({key:"ushare",data:res.data.data});
						wxshare(res.data.data,share);
					} else {
						console.log(res.data.message)
					}
				},
				fail: res => {
					console.log(res)
				}
			});
		}
	}
}

function wxshare(data,share){
	let config = data;
	/*api*/
	let jsApiList = [
		'updateAppMessageShareData',
		'updateTimelineShareData',
		'hideOptionMenu',
		'showOptionMenu',
		'chooseWXPay'
	];
	jWeixin.config({
		debug: false,
		appId: config.appid, //   
		timestamp: config.timestamp, //   
		nonceStr: config.noncestr, //   
		signature: config.signature, // 
		jsApiList: jsApiList // JS          
	});
	jWeixin.ready(() => {
		/*js*/
		let shareUrl = share.link+"?way=1&pid="+config.pid; //#
		/**/
		jWeixin.updateAppMessageShareData({
			title: share.title, //   
			desc: share.desc, //   
			link: shareUrl, // JS  
			imgUrl: share.imgUrl, //   
			success: () => {
				// share.success();
				console.log('')
			},
			cancel: () => {
				console.log('')
			}
		});
		/**/
		jWeixin.updateTimelineShareData({
			title: share.title, //   
			link: shareUrl, // JS  
			imgUrl: share.imgUrl, //   
			success: () => {
				// share.success();
				console.log('')
			},
			cancel: () => {
				console.log('')
			}
		});
	})
}

module.exports = {
	initShare: initShare,
	wxshare:wxshare
}
